    transition: all 0.3s ease;
  }
  .main-header .dropdown-item-wrap.opened .dropdown-arrow-btn {
    left: 0;
    transform: rotate(-180deg);
  }

  .main-header .dropdown-items-link {
    line-height: 24px;
  }

  /* CTAs */
  .dropdown-link-cta {
    display: none;
  }
  .dropdown-link-cta.mobile-show {
    display: block;
    margin-top: 20px;
  }
  .dropdown-link-cta .sk-block > h2 {
    font-size: 18px;
    line-height: 30px;
  }
  .dropdown-link-cta .sk-block > p {
    display: none;
  }

  .btn-default.clone {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    width: 100%;
    text-align: left;
    font-size: 16px;
    line-height: 20px;
  }
  .btn-default.clone::before,
  .btn-default.clone::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 9;
  }
  .btn-default.clone::before {
    right: 0;
    top: 0;
    bottom: 0;
    width: 50px;
    background: #002ea6;
  }
  .btn-default.clone::after {
    top: 50%;
    transform: translateY(-50%) rotate(-135deg);
    right: 20px;
    width: 15px;
    height: 15px;
    backface-visibility: hidden;
    border-bottom: 2px solid #fff;
    border-left: 2px solid #fff;
    border-right-color: #fff;
    border-top-color: #fff;
  }
  .btn-default.clone .btn-bg-el {
    border-radius: 0;
    background-color: #17fcc4;
  }

  #header .hbspt-form .hs-form-field label {
    background: #fff;
  }
  html.mobile-menu-opened .main-header .statement-analysis-btn.mobile-btn {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    text-align: left;
    font-size: 16px;
    line-height: 20px;
  }
  .main-header .statement-analysis-btn.mobile-btn .btn-bg-el {
    border-radius: 0;
  }
  .main-header .statement-analysis-btn.mobile-btn::before {
    content: '';
    display: block;
    position: absolute;
    z-index: 9;
    right: 0;
    top: 0;
    bottom: 0;
    width: 50px;
    background: #002ea6;
  }
  .main-header .statement-analysis-btn.mobile-btn::after {
    content: '';
    display: block;
    position: absolute;
    z-index: 9;
    top: 50%;
    transform: translateY(-50%) rotate(-135deg);
    right: 20px;
    width: 15px;
    height: 15px;
    backface-visibility: hidden;
    border-bottom: 2px solid #fff;
    border-left: 2px solid #fff;
    border-right-color: #fff;
    border-top-color: #fff;
  }

  .main-header .dropdown-arrow-btn:before {
    border-color: #002ea6;
  }

  .main-header .dropdown-item-wrap.opened .header-link-item.has-dropdown {
    color: #fc4a6b !important;
    padding-left: 30px;
    transition: all 0.3s ease;
  }
  .main-header .dropdown-item-wrap.opened .header-link-item.has-dropdown,
  .main-header .dropdown-item-wrap.opened .dropdown-arrow-btn:before {
    border-color: #fc4a6b;
  }

  .main-header
    .links-column:not(.contact-icons)
    > *:not(.column-heading):not(.dropdown-link-cta) {
    display: none;
  }

  .main-header .hbspt-form .hs-input:not(input[type='checkbox']) {
    color: #002ea6;
  }

  html.header-toggled-state
    .main-header
    .btn-default.btn-green-1
    .btn-bg-el:before {
    background-color: #002ea6 !important;
  }
  html.mobile-menu-opened .dropdown-link-cta .btn-default:hover .btn-txt,
  html.mobile-menu-opened .dropdown-link-cta .btn-default:focus .btn-txt {
    color: #fff;
  }

  .main-header .hbspt-form .hs-form-field {
    margin-bottom: 30px !important;
  }

  .main-header .solution-svg {
    margin-right: 10px;
  }

  .links-column.contact-icons {
    margin-bottom: 30px;
  }
}
@media (max-width: 768px) {
  .callback-popup-form .mobile-contact-form {
    margin: 0 auto;
    max-height: 100%;
    padding: 40px 0 60px;
    position: relative;
    width: 70%;
  }
  .callback-popup-form .hbspt-form {
    padding-bottom: 3rem;
  }
}
@media (max-width: 500px) {
  .nav-extras {
    padding-left: 9.4vw;
    padding-right: 9.4vw;
  }
}

html:not(.header-opened-dropdown):not(.mobile-menu-opened) {
  .main-header .dropdown-links-wrap {
    display: none;
  }
}
